import listActions from 'src/modules/discussion/list/discussionListActions';
import DiscussionService from 'src/modules/discussion/discussionService';
import Errors from 'src/modules/shared/error/errors';
import { i18n } from 'src/i18n';
import { getHistory } from 'src/modules/store';
import Message from 'src/view/shared/message';

const prefix = 'DISCUSSION_DESTROY';

const discussionDestroyActions = {
  DESTROY_STARTED: `${prefix}_DESTROY_STARTED`,
  DESTROY_SUCCESS: `${prefix}_DESTROY_SUCCESS`,
  DESTROY_ERROR: `${prefix}_DESTROY_ERROR`,

  DESTROY_ALL_STARTED: `${prefix}_DESTROY_ALL_STARTED`,
  DESTROY_ALL_SUCCESS: `${prefix}_DESTROY_ALL_SUCCESS`,
  DESTROY_ALL_ERROR: `${prefix}_DESTROY_ALL_ERROR`,

  doDestroy: (id) => async (dispatch) => {
    try {
      dispatch({
        type: discussionDestroyActions.DESTROY_STARTED,
      });

      await DiscussionService.destroyAll([id]);

      dispatch({
        type: discussionDestroyActions.DESTROY_SUCCESS,
      });

      Message.success(
        i18n('entities.discussion.destroy.success'),
      );

      dispatch(listActions.doFetchCurrentFilter());

      getHistory().push('/discussion');
    } catch (error) {
      Errors.handle(error);

      dispatch(listActions.doFetchCurrentFilter());

      dispatch({
        type: discussionDestroyActions.DESTROY_ERROR,
      });
    }
  },

  doDestroyAll: (ids) => async (dispatch) => {
    try {
      dispatch({
        type: discussionDestroyActions.DESTROY_ALL_STARTED,
      });

      await DiscussionService.destroyAll(ids);

      dispatch({
        type: discussionDestroyActions.DESTROY_ALL_SUCCESS,
      });

      if (listActions) {
        dispatch(listActions.doChangeSelected([]));
        dispatch(listActions.doFetchCurrentFilter());
      }

      Message.success(
        i18n('entities.discussion.destroyAll.success'),
      );

      getHistory().push('/discussion');
    } catch (error) {
      Errors.handle(error);

      dispatch(listActions.doFetchCurrentFilter());

      dispatch({
        type: discussionDestroyActions.DESTROY_ALL_ERROR,
      });
    }
  },
};

export default discussionDestroyActions;
