import DiscussionService from 'src/modules/discussion/discussionService';
import Errors from 'src/modules/shared/error/errors';
import { getHistory } from 'src/modules/store';

const prefix = 'DISCUSSION_VIEW';

const discussionViewActions = {
  FIND_STARTED: `${prefix}_FIND_STARTED`,
  FIND_SUCCESS: `${prefix}_FIND_SUCCESS`,
  FIND_ERROR: `${prefix}_FIND_ERROR`,

  doFind: (id) => async (dispatch) => {
    try {
      dispatch({
        type: discussionViewActions.FIND_STARTED,
      });

      const record = await DiscussionService.find(id);

      dispatch({
        type: discussionViewActions.FIND_SUCCESS,
        payload: record,
      });
    } catch (error) {
      Errors.handle(error);

      dispatch({
        type: discussionViewActions.FIND_ERROR,
      });

      getHistory().push('/discussion');
    }
  },
};

export default discussionViewActions;
